<template>
  <el-dialog title="" v-model="show" top="10%" size="large" >
    <el-form :model="form" :rules="rules" ref="ruleForm" label-width="120px">
      <el-col :span="24">
      <el-form-item label="" prop="company_id">
        <el-input v-model="form.company_id" placeholder="ID":disabled="true"></el-input>
      </el-form-item>
      </el-col>
      <el-col :span="24">
      <el-form-item label="" prop="buy_from_company_id">
        <el-input v-model="form.buy_from_company_id" placeholder="ID":disabled="true"></el-input>
      </el-form-item>
      </el-col>
      <el-col :span="24">
      <el-form-item label="" prop="sale_to_company_id">
        <el-input v-model="form.sale_to_company_id" placeholder="ID":disabled="true"></el-input>
      </el-form-item>
      </el-col>
      <el-col :span="24">
      <el-form-item label="" prop="company_name">
        <el-input v-model="form.company_name"></el-input>
      </el-form-item>
      </el-col>
      <el-col :span="24">
      <el-form-item label="" prop="company_tel">
        <el-input v-model="form.company_tel"  type="number"></el-input>
      </el-form-item>
      </el-col>
      <el-col :span="24">
      <el-form-item label="" prop="company_address">
        <el-input v-model="form.company_address" placeholder=""></el-input>
      </el-form-item>
      </el-col>
      <el-col :span="24">
      <el-form-item label="" prop="company_mail">
        <el-input v-model="form.company_mail" placeholder=""></el-input>
      </el-form-item>
      </el-col>
      </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="display"> </el-button>
      <el-button type="primary" @click="AddCompany"></el-button>
    </div>
  </el-dialog>
</template>
<script type="text/javascript">
  export default {
    data() {
      return {
        // 
        form: {
          company_id: '',
          buy_from_company_id: '',
          sale_to_company_id: '',
          company_name: '',
          company_tel: '',
          company_address: '',
          company_mail: ''
        },
        // 
        // trigger: blur  change 
        rules: {
          company_name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
          ],
          company_tel: [
            { required: true, message: '', trigger: 'blur' },
            { min: 9, max: 11, message: '', trigger: 'blur' }
          ],
          company_address: [
            { type: 'text', required: true, message: '', trigger: 'change' }
          ],
          company_mail: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: 'blur' }
          ]
        }
      }
    },
    props: {
      show: {
        type: Boolean,
        default() {
          return false
        }
      }
    },
    methods: {
      display: function () {
        this.$message({
          showClose: true,
          message: ''
        })
        this.show = false
        this.$emit('display')
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handlePreview(file) {
        console.log(file)
      },
      AddCompany() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        })
        console.log('sublime')
        let url = './api/company/add'
        this.$http.post(url, this.form)
        .then((response) => {
          this.$set('form', {})
        }, (response) => {
          console.log('error')
        })
        this.show = false
      }
    }
  }
</script>
<style type="text/css">
  
</style>



